import React, { useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import {
  Box,
  Button,
  TextField,
  Typography,
  Paper,
  CircularProgress,
} from "@mui/material";
import { fetchForgotChangePassword } from "../../actions/CallApi/LoginFetch";

const ResetPasswordPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const user_name = location.state?.username || "";
  const email = location.state?.email || "";
  const reset_code = location.state?.reset_code || "";

  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const [passwordError, setPasswordError] = useState("");
  const [confirmError, setConfirmError] = useState("");
  const [generalError, setGeneralError] = useState("");

  const [loading, setLoading] = useState(false);

  const handleUpdate = async () => {
    setPasswordError("");
    setConfirmError("");
    setGeneralError("");

    let is_valid = true;
    if (!password.trim()) {
      setPasswordError("");
      is_valid = false;
    } else if (password.length < 6) {
      setPasswordError("6");
      is_valid = false;
    }

    if (!confirmPassword.trim()) {
      setConfirmError("");
      is_valid = false;
    } else if (password !== confirmPassword) {
      setConfirmError("");
      is_valid = false;
    }

    if (!is_valid) return;

    setLoading(true);
    try {
      await fetchForgotChangePassword({user_name, email, reset_code, new_password:password});
      alert("Mt khu  c cp nht thnh cng! Vui lng ng nhp li.");
      navigate("/login");
    } catch (err) {
      setGeneralError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box
      sx={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        minHeight: "100vh",
        bgcolor: "#fb5050ff",
      }}
    >
      <Paper sx={{ p: 4, width: 400, borderRadius: 2, boxShadow: 3 }}>
        <Typography variant="h5" mb={3} textAlign="center" color="#b71c1c">
          i mt khu
        </Typography>

        <TextField
          label="Mt khu mi"
          variant="outlined"
          fullWidth
          margin="normal"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          error={!!passwordError}
          helperText={passwordError}
        />

        <TextField
          label="Xc nhn mt khu"
          variant="outlined"
          fullWidth
          margin="normal"
          type="password"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
          error={!!confirmError}
          helperText={confirmError}
        />

        {generalError && (
          <Typography color="error" variant="body2" sx={{ mt: 1 }}>
            {generalError}
          </Typography>
        )}

        <Button
          variant="contained"
          fullWidth
          sx={{ mt: 2, bgcolor: "#f28b82", "&:hover": { bgcolor: "#e57373" } }}
          onClick={handleUpdate}
          disabled={loading}
        >
          {loading ? (
            <CircularProgress size={24} color="inherit" />
          ) : (
            "Update Password"
          )}
        </Button>
      </Paper>
    </Box>
  );
};

export default ResetPasswordPage;
