export const handleApiResponse = (response, defaultErrorMessage = "API Error") => {
  const res = response.data;
  if (![200, 201].includes(res.code) || !res.data) {
    if (res.code !== 500) {
      throw new Error(res.message || defaultErrorMessage);
    }else{
      throw new Error(defaultErrorMessage);
    }
  }
  return res.data;
};

export const handleApiError = (err) => {
  const message =
    err.response?.data?.message ||
    err.message ||
    " (Network error)";
  throw new Error(message);
};