import axios from "axios";
import { API_USER } from "../../utilities/constant";
import { handleApiResponse, handleApiError } from "../../utilities/handlerApi";

export const fetchLogin = async (data) => {
  try {
    const response = await axios.post(`${API_USER}/user-admin/login`, data);
    return handleApiResponse(response, "");
  } catch (err) {
    handleApiError(err);
  }
};

export const fetchCurrentUser = async ({ access_token, token_type }) => {
  try {
    const response = await axios.get(`${API_USER}/user-admin/infor`, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "");
  } catch (err) {
    handleApiError(err);
  }
};

export const fetchRefreshToken = async ({ refresh_token }) => {
  try {
    const response = await axios.get(`${API_USER}/user-admin/refresh-token`, {
      params: { refresh_token },
    });
    return handleApiResponse(response, "Failed to refresh token");
  } catch (err) {
    handleApiError(err);
  }
};

export const fetchChangePassword = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(
      `${API_USER}/user-admin/change-password`,
      data,
      {
        headers: {
          Authorization: `${token_type} ${access_token}`,
        },
      }
    );
    return handleApiResponse(response, "");
  } catch (err) {
    handleApiError(err);
  }
};

