import React from "react";
import { Drawer, Toolbar, List, ListItem, ListItemText } from "@mui/material";
import LogoutIcon from "@mui/icons-material/Logout";
import { Link, useNavigate } from "react-router-dom";
import {
  ROLE_TYPE_SUPERADMIN,
  ROLE_TYPE_ADMIN,
  ROLE_TYPE_CHECKIN,
  ROLE_TYPE_ORDER
} from "../../utilities/constant";
import { useAuth } from "../../context/AuthContext";

const drawerWidth = 190;

const Sidebar = () => {
  const navigate = useNavigate();
  const { clearAuth, user } = useAuth();

  const handleLogout = () => {
    clearAuth();
    navigate("/login");
  };
  return (
    <Drawer
      variant="permanent"
      sx={{
        width: drawerWidth,
        flexShrink: 0,
        [`& .MuiDrawer-paper`]: {
          width: drawerWidth,
          boxSizing: "border-box",
        },
      }}
    >
      <Toolbar />
      <List>
        <ListItem button component={Link} to="/">
          <ListItemText primary="Home" />
        </ListItem>
        {(user?.role_type === ROLE_TYPE_SUPERADMIN || user?.role_type === ROLE_TYPE_CHECKIN)  && (
            <ListItem button component={Link} to="/check-in-out">
              <ListItemText primary="CheckInOut" />
            </ListItem>
          )}
        {(user?.role_type === ROLE_TYPE_SUPERADMIN ||
          user?.role_type === ROLE_TYPE_ADMIN) && (
            <ListItem button component={Link} to="menu">
              <ListItemText primary="Menu" />
            </ListItem>
          )}
        {(user?.role_type === ROLE_TYPE_SUPERADMIN ||
          user?.role_type === ROLE_TYPE_ADMIN) && (
            <ListItem button component={Link} to="category">
              <ListItemText primary="Category" />
            </ListItem>
          )}

        <ListItem button component={Link} to="schedule">
          <ListItemText primary="Schedule" />
        </ListItem>
        {(user?.role_type === ROLE_TYPE_SUPERADMIN || user?.role_type === ROLE_TYPE_ORDER) && (
            <ListItem button component={Link} to="order">
              <ListItemText primary="Order" />
            </ListItem>
          )}
        {user?.role_type === ROLE_TYPE_SUPERADMIN && (
          <ListItem button component={Link} to="user">
            <ListItemText primary="User" />
          </ListItem>
        )}
        {user?.role_type === ROLE_TYPE_SUPERADMIN && (
          <ListItem button component={Link} to="holiday">
            <ListItemText primary="Holiday" />
          </ListItem>
        )}
        <ListItem button component={Link} to="user-information">
          <ListItemText primary="User Information" />
        </ListItem>
        <ListItem button onClick={handleLogout}>
          <LogoutIcon sx={{ marginRight: 1 }} />
          <ListItemText primary="Logout" />
        </ListItem>
      </List>
    </Drawer>
  );
};

export default Sidebar;
