import React from 'react';
import { Button, TextField, Typography, CircularProgress } from '@mui/material';

const LoginForm = ({
  username,
  password,
  usernameError,
  passwordError,
  error,
  loading,
  onUsernameChange,
  onPasswordChange,
  onSubmit
}) => {
  return (
    <form onSubmit={onSubmit}>
      <TextField
        label=""
        variant="outlined"
        fullWidth
        margin="normal"
        value={username}
        onChange={onUsernameChange}
      />
      {usernameError && (
        <Typography color="error" variant="body2" sx={{ mt: 1, mb: 1 }}>
          {usernameError}
        </Typography>
      )}

      <TextField
        label=""
        variant="outlined"
        type="password"
        fullWidth
        margin="normal"
        value={password}
        onChange={onPasswordChange}
      />
      {passwordError && (
        <Typography color="error" variant="body2" sx={{ mt: 1, mb: 1 }}>
          {passwordError}
        </Typography>
      )}

      {error && (
        <Typography color="error" variant="body2" sx={{ mt: 1, mb: 1 }}>
          {error}
        </Typography>
      )}

      <Button
        type="submit"
        variant="contained"
        fullWidth
        sx={{
          mt: 2,
          bgcolor: '#f28b82',
          color: 'white',
          '&:hover': { bgcolor: '#e57373' },
        }}
        disabled={loading}
      >
        {loading ? <CircularProgress size={24} color="inherit" /> : ''}
      </Button>
    </form>
  );
};

export default LoginForm;
