import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Button, TextField, Typography, Paper, CircularProgress } from '@mui/material';
import { fetchSendCode, fetchVerifyCode } from '../../actions/CallApi/LoginFetch';

const ForgotPasswordPage = () => {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [reset_code, setResetCode] = useState('');

  const [usernameError, setUsernameError] = useState('');
  const [emailError, setEmailError] = useState('');
  const [codeError, setCodeError] = useState('');
  const [generalError, setGeneralError] = useState('');

  const [loadingSend, setLoadingSend] = useState(false);
  const [loadingConfirm, setLoadingConfirm] = useState(false);
  const [sendDisabled, setSendDisabled] = useState(false);
  const [countdown, setCountdown] = useState(60);

  const navigate = useNavigate();

  useEffect(() => {
    let timer;
    if (sendDisabled) {
      timer = setInterval(() => {
        setCountdown(prev => {
          if (prev <= 1) {
            clearInterval(timer);
            setSendDisabled(false);
            return 60;
          }
          return prev - 1;
        });
      }, 1000);
    }
    return () => clearInterval(timer);
  }, [sendDisabled]);

  const handleSendCode = async () => {
    // Reset li
    setUsernameError('');
    setEmailError('');
    setGeneralError('');

    let is_valid = true;
    if (!username.trim()) {
      setUsernameError('');
      is_valid = false;
    }
    if (!email.trim()) {
      setEmailError('');
      is_valid = false;
    }

    if (!is_valid) return;

    setLoadingSend(true);
    try {
      await fetchSendCode({user_name: username, email});
      // alert('Code  c gi thnh cng. Vui lng kim tra email.');
      setSendDisabled(true);
    } catch (err) {
      setGeneralError(err.message);
    } finally {
      setLoadingSend(false);
    }
  };

  const handleConfirm = async () => {
    setCodeError('');
    setGeneralError('');

    if (!reset_code.trim()) {
      setCodeError('');
      return;
    }

    setLoadingConfirm(true);
    try {
      await fetchVerifyCode({user_name: username, email, reset_code});
      // alert('Xc nhn thnh cng, chuyn sang i mt khu!');
      navigate('/reset-password', { state: { username, email, reset_code } });
    } catch (err) {
      setGeneralError(err.message);
    } finally {
      setLoadingConfirm(false);
    }
  };

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        bgcolor: '#fb5050ff',
      }}
    >
      <Paper sx={{ p: 4, width: 400, borderRadius: 2, boxShadow: 3 }}>
        <Typography variant="h5" mb={3} textAlign="center" color="#b71c1c">
          Qun mt khu
        </Typography>

        <TextField
          label=""
          variant="outlined"
          fullWidth
          margin="normal"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          error={!!usernameError}
          helperText={usernameError}
        />

        <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 1, mt: 1 }}>
          <Box sx={{ flex: 1 }}>
            <TextField
              label=""
              variant="outlined"
              fullWidth
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              error={!!emailError}
            />
            {emailError && (
              <Typography color="error" variant="body2" sx={{textAlign: 'center', mt: 0.5 }}>
                {emailError}
              </Typography>
            )}
          </Box>
          <Button
            variant="contained"
            sx={{ mt: '22px', bgcolor: '#f28b82', '&:hover': { bgcolor: '#e57373' }, px: 2, py: 0.5, fontSize: '0.8rem', minWidth: '80px' }}
            onClick={handleSendCode}
            disabled={sendDisabled || loadingSend}
          >
            {loadingSend ? <CircularProgress size={16} color="inherit" /> : sendDisabled ? `${countdown}s` : 'Send'}
          </Button>
        </Box>

        <TextField
          label=""
          variant="outlined"
          fullWidth
          margin="normal"
          value={reset_code}
          onChange={(e) => setResetCode(e.target.value)}
          error={!!codeError}
          helperText={codeError}
        />

        {generalError && (
          <Typography color="error" variant="body2" sx={{textAlign: 'center', mt: 1 }}>
            {generalError}
          </Typography>
        )}

        <Button
          variant="contained"
          fullWidth
          sx={{ mt: 2, bgcolor: '#f28b82', '&:hover': { bgcolor: '#e57373' } }}
          onClick={handleConfirm}
          disabled={loadingConfirm}
        >
          {loadingConfirm ? <CircularProgress size={24} color="inherit" /> : 'Confirm'}
        </Button>
      </Paper>
    </Box>
  );
};

export default ForgotPasswordPage;
