import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { TOKEN_KEY, TOKEN_TYPE_KEY, REFRESH_TOKEN_KEY, IS_LOGGED_IN } from "../utilities/constant";
import { fetchCurrentUser, fetchRefreshToken } from '../actions/CallApi/LoginFetch';

const AuthContext = createContext(null);
export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isChecking, setIsChecking] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const getToken = () => ({
    access_token: localStorage.getItem(TOKEN_KEY),
    token_type: localStorage.getItem(TOKEN_TYPE_KEY),
    refresh_token: localStorage.getItem(REFRESH_TOKEN_KEY),
  });

  const clearAuth = () => {
    localStorage.removeItem(TOKEN_KEY);
    localStorage.removeItem(TOKEN_TYPE_KEY);
    localStorage.removeItem(REFRESH_TOKEN_KEY);
    localStorage.removeItem(IS_LOGGED_IN);
    setUser(null);
    setIsAuthenticated(false);
  };

  const login = async ({ access_token, token_type }) => {
    localStorage.setItem(TOKEN_KEY, access_token);
    localStorage.setItem(TOKEN_TYPE_KEY, token_type);

    try {
      const userData = await fetchCurrentUser({ access_token, token_type });
      setUser(userData);
      setIsAuthenticated(true);
      localStorage.setItem(IS_LOGGED_IN, "true");
    } catch (err) {
      clearAuth();
      throw err;
    }
  };

  const refreshToken = async () => {
    const { refresh_token } = getToken();
    if (!refresh_token) throw new Error("No refresh token available");

    const newToken = await fetchRefreshToken({ refresh_token });
    const { access_token, token_type } = newToken;

    localStorage.setItem(TOKEN_KEY, access_token);
    localStorage.setItem(TOKEN_TYPE_KEY, token_type);

    return { access_token, token_type };
  };

  const callApiWithToken = useCallback(async (apiFunc, ...args) => {
    let { access_token, token_type } = getToken();
    if (!access_token) throw new Error("No access token");

    try {
      return await apiFunc({ access_token, token_type }, ...args);
    } catch (err) {
      if (err?.response?.status === 401) {
        try {
          const newToken = await refreshToken();
          return await apiFunc({ access_token: newToken.access_token, token_type: newToken.token_type }, ...args);
        } catch {
          clearAuth();
          throw new Error("Session expired");
        }
      } else {
        throw err;
      }
    }
  }, []);

  useEffect(() => {
    const initAuth = async () => {
      const { access_token, token_type } = getToken();
      if (!access_token) {
        setIsChecking(false);
        return;
      }
      try {
        const userData = await fetchCurrentUser({ access_token, token_type });
        setUser(userData);
        setIsAuthenticated(true);
      } catch {
        clearAuth();
      } finally {
        setIsChecking(false);
      }
    };

    initAuth();
  }, []);

  return (
    <AuthContext.Provider value={{
      user,
      isAuthenticated,
      isChecking,
      login,
      clearAuth,
      callApiWithToken
    }}>
      {children}
    </AuthContext.Provider>
  );
};
