import React, { lazy } from "react";
import RequireAdmin from "../../components/RequireAdmin"
import NotFound from "../../pages/NotFound";
import { ROLE_TYPE_SUPERADMIN, ROLE_TYPE_ADMIN, ROLE_TYPE_CHECKIN, ROLE_TYPE_ORDER } from "../constant";

const HolidayPagePage = lazy(() => import("../../pages/admin/HolidayPage"));
const HolidayAddOrEditPage = lazy(() => import("../../components/HolidayPage/HolidayAddPage"));

const MenuPage = lazy(() => import("../../pages/admin/MenuPage"));
const MenuAddOrEditPage = lazy(() => import("../../components/MenuPage/MenuAddPage"));

const OrderPage = lazy(() => import("../../pages/admin/OrderPage"));

const UserPage = lazy(() => import("../../pages/admin/UserPage"));
const UserAddPage = lazy(() => import("../../components/User/UserAddPage"));

const CategoryPage = lazy(() => import("../../pages/admin/CategoryPage"));
const CategoryAddOrEditPage = lazy(() => import("../../components/CategoryPage/CategoryAddPage"));

const SchedulePage = lazy(() => import("../../pages/admin/SchedulePage"));
const ScheduleAddOrEditPage = lazy(() => import("../../components/SchedulePage/ScheduleAddPage"));

const UserInforPage = lazy(() => import("../../pages/admin/UserInfor"));
const ScheduleCheckInOutPage = lazy(() => import("../../pages/admin/ScheduleCheckInOutPage"));

const HomePage = lazy(() => import("../../pages/HomePage"));

const adminRoutes = [
  {
    path: "",
    children: [
      { path: "", element: <HomePage /> },
      {
        path: "menu",
        children: [
          { path: "", element: <MenuPage /> },
          { path: "form", element: <MenuAddOrEditPage /> },
        ],
      },
      {
        path: "schedule",
        children: [
          { path: "", element: <SchedulePage /> },
          { path: "form", element: <ScheduleAddOrEditPage /> },
        ],
      },
      {
        path: "category",
        children: [
          { path: "", element: <CategoryPage /> },
          { path: "form", element: <CategoryAddOrEditPage /> },
        ],
      },
      {
        path: "order",
        children: [{ path: "", element: <RequireAdmin Permission={[ROLE_TYPE_SUPERADMIN, ROLE_TYPE_ADMIN, ROLE_TYPE_ORDER]} > <OrderPage/> </RequireAdmin> }],
      },
      {
        path: "user",
        children: [
          { path: "", element: <RequireAdmin Permission={[ROLE_TYPE_SUPERADMIN]}><UserPage /></RequireAdmin>},
          { path: "form", element: <UserAddPage /> },
        ],
      },
      {
        path: "holiday",
        children: [
          { path: "", element: <RequireAdmin Permission={[ROLE_TYPE_SUPERADMIN]}><HolidayPagePage /></RequireAdmin>},
          { path: "form", element: <HolidayAddOrEditPage /> },
        ],
      },
      {
        path: "user-information",
        children: [{ path: "", element: <UserInforPage /> }],
      },
      {
        path: "check-in-out",
        children: [{ path: "", element: <RequireAdmin Permission={[ROLE_TYPE_SUPERADMIN, ROLE_TYPE_ADMIN, ROLE_TYPE_CHECKIN]}><ScheduleCheckInOutPage /> </RequireAdmin>}],
      },
      {
        path: "*",
        element: <NotFound />
      }
    ],
  },
];

export default adminRoutes;
